* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  color: #fff;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  left: 0;
  right: 0;
}

.header .logo {
  width: 40px;
  height: 40px;
}

.header .nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.header .nav__list .nav__item {
  height: 100%;
  margin-left: 20px;
  text-decoration: none;
}

.header .nav__list .nav__item:first-child {
  margin-left: 0;
}

.header .nav__list .nav__item .nav__link {
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.header .nav__list .nav__item .nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.header .nav__list .nav__item .nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
  bottom: 0;
  right: 0;
}

.header .nav__list .nav__item .nav__link--laptops {
  color: #060b35;
  text-decoration-color: #060b35;
}

.header .nav__list .nav__item .nav__link:hover {
  color: #00acdc;
  text-decoration-color: #00acdc;
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__top {
  justify-content: center;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  display: block;
}

.card__bottom {
  flex-direction: column;
  margin-top: 40px;
  display: flex;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__quality {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding: 11px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
